
import { computed, defineComponent, PropType } from 'vue'

export default defineComponent({
  name: "IconButton",
  props: {
    icon: {
      type: Array as PropType<string[]>,
      required: true,
    },
    url: {
      type: String,
      default: null,
    },
    size: {
      type: String,
      default: "small",
    }
  },
  setup(props) {
    const size = computed(() => {
      return "size_" + props.size
    })

    return {
      fontSize: size
    }
  }
})
